.contacts {
	margin: 164px auto 52px;
	position: relative;
	@include sm {
		margin: 54px AUTO 192PX; }
	&__container {
		position: relative;
		display: flex;
		justify-content: space-between;
		@include sm {
			flex-direction: column; } }
	&__direct-links {
		flex-basis: 370px;
		margin-top: 71px;
		@include md {
			flex-basis: 35%; }
		@include sm {
			order: 2;
			flex-basis: 100%;
			margin-top: 55px; } }
	&__form {
		flex-basis: 500px;
		@include md {
			flex-basis: 60%; }
		@include sm {
			order: 1;
			flex-basis: 100%; } }
	&__btn {
		margin-bottom: 24px;
		.icon {
			display: inline-block;
			margin-right: 10px; } }
	&__email {
		display: block;
		margin-top: 4px;
		font-size: 18px;
		font-weight: 600;
		text-align: center;
		color: #ffffff; }
	&__cube-left {
		position: absolute;
		top: 92px;
		left: -373px;
		@include sm {
			top: 100%;
			left: 80%; } }

	&__cube-right {
		position: absolute;
		top: -62px;
		right: -307px;
		@include sm {
			right: -70px;
			top: -70px; } } }

.form {
	color: #fff;
	padding: 48px;
	border: solid 16px #56ecf6;
	border-radius: 8px;
	@include sm {
		padding: 36px;
		padding-left: 24px;
		border: solid 12px #56ecf6; }
	&__title {
		@include sm {
 } }			// letter-spacing: -4px
	&__input {
		display: block;
		width: 100%;
		// margin-right: 20px
		padding: 20px 9px;
		border: none;
		outline: none;
		background-color: inherit;
		border-bottom: 2px solid rgba(#56ecf6, 0.69);
		margin: 8px auto 48px;
		font-size: 24px;
		font-weight: 600;
		color: #fff;
		@include sm {
			font-size: 16px;
			padding: 14px 9px; }
		&::placeholder {
			font-size: 24px;
			font-weight: 600;
			color: #fff;
			@include sm {
				font-size: 16px; } }
		&:focus {
			border-bottom: 2px solid rgba(#56ecf6, 1); }
		@include sm {
			margin: 12px auto 24px; } } }




