.howitworks {
    position: relative;
    z-index: 10;
    color: $main-color-dark;
    // &__container
    //     padding: 88px 100px
    //     +md
    //         padding: 40px 24px 60px
    //     +sm
    //         padding: 32px 10px 40px

    &__title {
        margin-bottom: 30px;
        line-height: 1;
        @include md {
            margin-bottom: 36px; }
        @include sm {
            text-align: center;
            margin-bottom: 27px; } }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        @include sm {
            flex-direction: column; } }
    &__column {
        flex-basis: 46%;
        display: flex;
        flex-direction: column;
        @include md {
            flex-basis: 49%; } } }

.work {
    position: relative;
    &:not(:last-child) {
        margin-bottom: 38px;
        @include md {
            margin-bottom: 20px; }
        @include sm {
           margin-bottom: 22px; } }
    @include sm {
        &:last-child {
            margin-bottom: 22px; } }
    &__icon {
        position: absolute;
        top: 9px;
        left: 0;
        @include md {
            top: 2px;
            width: 48px; } }
    &__text {
        margin-left: 75px;
        font-size: 18px;
        font-weight: 300;
        line-height: 2;
        // &::first-line
        //     line-height: 1
        @include md {
            font-size: 14px;
            line-height: 22px;
            margin-left: 64px; } } }

