body {
	background: $main-color-light;
	// +css-lock(16, 20, 600, 2000)
	line-height: 1.4;
	font-family: $Montserrat, sans-serif;
	overflow-x: hidden;
	@include smooth-scroll;
	@include smooth-font;
	@include prevent-text; }
* {
	box-sizing: border-box;
	box-shadow: 0 0 0 1px #00f;
	// &::-webkit-scrollbar
 } // // 	display: none;


image {
	max-width: 100%;
	height: auto; }

a {
	text-decoration: none;
	color: inherit; }

// +smooth-scroll

// ===== LAYOUT =====

.container-thin {
	max-width: 970px;
	margin: 0 auto;
	position: relative;
	@include md {
		width: auto;
		margin: 0 60px; }
	@include sm {
		margin: 0 15px; } }

.container {
	margin: 0 auto;
	padding: 0 15px;
	@include md {
		padding: 0 36px; }
	@include sm {
		padding: 0 10px; } }

.container-inner {
	margin: 0 auto;
	max-width: 1170px;
	// +md
	// 	padding: 0 36px
	// +sm
 }	// 	padding: 0 5px

.white-rounded-container--outer {
	margin: 0 auto;
	// width: 1170px
	max-width: 1170px;
	background-color: #fff;
	box-shadow: 0px 24px 72px 0 rgba(29, 53, 78, 0.25);
	border-radius: 16px;
	padding: 88px 100px;
	@include md {
		padding: 36px 36px; }
	@include sm {
		padding: 32px 10px; } }

.white-rounded-container--inner {
	max-width: 970px;
	margin: 0 auto;
	// padding: 88px 0
	position: relative; }



// ===== BUTTONS =====
.btn {
	border-radius: 32px;
	height: 64px;
	margin: 0 auto;
	line-height: 64px;
	font-size: 18px;
	font-weight: 600;
	text-align: center;
	@include md {
		font-size: 16px;
		height: 48px;
		line-height: 48px; } }


.btn-inline {
	display: inline-block;
	padding: 0 65px;
	@include md {
		padding: 0 48px; } }



.btn-block {
	display: block;
	width: 100%; }

.btn-dark {
	background-color: $main-color-dark;
	color: #fff; }

.btn-light {
	background-color: #fff;
	color: $main-color-dark; }

.btn-sm-big {
	@include sm {
		height: 66px;
		line-height: 66px; } }

// ===== TITLES =====
.title--large {
	font-size: 60px;
	font-weight: 600;
	line-height: 1;
	@include md {
		font-size: 48px; }
	@include sm {
		font-size: 36px; } }


.title--xlarge {
	font-size: 72px;
	font-weight: 600;
	line-height: 1;
	@include md {
		font-size: 48px; }
	@include sm {
		font-size: 36px; } }

.title--thin {
	font-weight: 200; }
