.faq {
    position: relative;
    &__container {
        position: relative;
        @include sm {
            padding-left: 24px; } }
    &__title {
        margin-bottom: 52px;
        margin-top: -8px;
        @include sm {
            margin-bottom: 27px; } }
    &__cube {
        position: absolute;
        top: 88px;
        left: -189px;
        @include md {
            top: -50px;
            right: 50px;
            left: auto; }
        @include sm {
            top: -116px;
            left: -78px; } } }



.question {
    position: relative;
    padding-left: 52px;
    @include sm {
        padding-left: 42px;
        padding-right: 10px; }
    &:not(:last-child) {
        margin-bottom: 28px;
        @include sm {
            margin-bottom: 24px; } }

    &__question {
        margin-bottom: 24px;
        font-size: 30px;
        font-weight: 600;
        line-height: 1.6;
        color: $main-color-dark;
        @include md {
            font-size: 24px;
            margin-bottom: 24px;
            line-height: 36px; }
        @include sm {
            font-size: 18px;
            margin-bottom: 20px;
            line-height: 24px; } }
    &__icon {
        position: absolute;
        top: 0;
        left: 0;
        width: 36px;
        height: 36px;
        cursor: pointer;
        @include sm {
            width: 28px;
            top: -8px; }
        svg {
            height: 100%;
            width: 100%;
            fill: $main-color-light; } }
    &__answer {
        display: none; }
    &-active &__icon {
        transform: rotate(-90deg); }
    &-active &__question {
        margin-bottom: 9px; }
    &-active &__answer {
        display: block;
        line-height: 2;
        font-size: 18px;
        @include sm {
            font-size: 14px;
            line-height: 22px; } } }

