.hero {
    position: relative;
    background-position: 0 0;
    background-repeat: no-repeat;
    z-index: 150;
    &__text {
        position: relative;
        margin-left: 374px;
        text-align: center;
        // width: 45%
        color: #ffffff;
        font-weight: 600;
        z-index: 10;
        @include md {
            margin-left: 320px; }
        @include sm {
            margin-left: 0; }
        // +sm
        //     width: auto
 }        //     margin-left: 0
    &__title {
        font-size: 72px;
        line-height: 1.17;
        @include md {
            font-size: 48px;
            line-height: 1; }
        @include sm {
            line-height: 1;
            margin-bottom: 10px; } }
    &__descr {
        margin-bottom: 20px;
        margin-top: 12px;
        font-size: 30px;
        line-height: 1.6;
        @include md {
            font-size: 18px;
            margin-bottom: 16px; }
        @include sm {
            margin-bottom: 28px; } }
    &__tv-wrapper {
        position: relative;
        margin-top: 209px;
        @include md {
            margin-top: 164px; }
        @include sm {
            margin-top: 310px; } }
    &__cube-light-shaded {
        position: absolute;
        right: -158px;
        top: -267px; } }

.tv {
    position: relative;
    // max-width: 1170px
    height: 720px;
    margin: 0 auto;
    background-color: #fff;
    box-shadow: 0px 24px 72px 0 rgba(29, 53, 78, 0.25);
    border-radius: 16px;
    &__inner {
        position: absolute;
        top: 48px;
        left: 48px;
        bottom: 48px;
        right: 48px;
        background-color: #000;
        border-radius: 16px; }
    &__play {
        width: 120px;
        height: 120px;
        @include vcenter;
        cursor: pointer;
        svg {
            height: 100%;
            width: 100%; } }
    &__robot {
        position: absolute;
        width: 1200px;
        top: -759px;
        left: -100px;
        z-index: 5;
        pointer-events: none;
        img {
            max-width: 100%; } } }




$tv-padding-md-x: 23px;
$tv-padding-md-y: 27px;

@include md {
    .tv {
        // width: 90vw
        height: 56vw;
        // height: 475px
        margin: 0 auto;
        position: relative;
        background-color: #fff;
        box-shadow: 0px 24px 72px 0 rgba(29, 53, 78, 0.25);
        border-radius: 16px;
        &__inner {
            position: absolute;
            top: $tv-padding-md-y;
            left: $tv-padding-md-x;
            bottom: $tv-padding-md-y;
            right: $tv-padding-md-x;
            background-color: #000;
            border-radius: 16px; }
        &__play {
            width: 120px;
            height: 120px;
            @include vcenter;
            svg {
                height: 100%;
                width: 100%; } }
        &__robot {
                top: -564px;
                left: -59px;
                width: 819px;
                img {
                    width: 100%; } } } }

$tv-padding-sm-x: 10px;
$tv-padding-sm-y: 10px;

@include sm {
    .tv {
        &__inner {
            top: $tv-padding-sm-y;
            left: $tv-padding-sm-x;
            bottom: $tv-padding-sm-y;
            right: $tv-padding-sm-x; }
        &__play {
            width: 60px;
            height: 60px; }
        &__robot {
            position: absolute;
            top: -427px;
            left: -53px;
            width: 500px; } } }

